import React from 'react';
import './styles/login-page.css';

export default function LoginPage() {
    return (
        <div id="LoginPageContainer" style={{ textAlign: "center" }}>
            <h2 id="header-title">Welcome to best app on Earth!</h2>  
            <p id="description">Find Smithy is an application that brings the community together. Not sure if that <i>Nintendo Switch</i> is still available at the local <i>Walmart</i>? Add what you want to our communal list and allow the crowd sourcing to do its magic. Check it out by logging in with Google!</p>
        </div>
    )
}   



